.page {
  height: 100%;
}

.page__body {
  min-height: 100%;
  margin: 0;
  font-family: "Arial", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: $special-dark-grey;
  background-color: #ffffff;

  @media (min-width: $tablet-width) {
    font-size: 16px;
    line-height: 24px;
  }
}
