*,
*::after,
*::before {
  box-sizing: border-box;
}

@font-face {
  font-family: "Oswald";
  font-weight: 400;
  font-style: normal;
  src: url("../fonts/oswaldregular.woff") format("woff"), url("../fonts/oswaldregular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-weight: 500;
  font-style: normal;
  src: url("../fonts/oswaldmedium.woff") format("woff"), url("../fonts/oswaldmedium.woff2") format("woff2");
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.decorated-title {
  position: relative;
  display: inline-block;
  padding-right: 22px;
  background-color: #ffffff;
  font-family: "Oswald", sans-serif;
  font-size: 24px;
  line-height: 26px;
  font-weight: 400;
  color: #000000;
}
@media (min-width: 768px) {
  .decorated-title {
    font-size: 36px;
    line-height: 36px;
    padding-right: 36px;
    padding-left: 36px;
  }
}
@media (min-width: 1280px) {
  .decorated-title {
    left: -36px;
  }
}
.decorated-title::before {
  position: absolute;
  display: block;
  left: 100%;
  top: 17px;
  height: 2px;
  width: 100vw;
  background-color: #68b738;
  content: "";
  z-index: -1;
}
@media (min-width: 768px) {
  .decorated-title::before {
    left: -20vw;
    width: 120vw;
    top: 23px;
  }
}

.page {
  height: 100%;
}

