@media (min-width: 768px) {
  .program__title {
    margin-bottom: 54px;
  }
}
@media (min-width: 1280px) {
  .program__title {
    margin-bottom: 56px;
  }
}

.program__text {
  margin-bottom: 39px;
  font-family: "Oswald", sans-serif;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  color: #000000;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .program__text {
    margin-bottom: 66px;
    max-width: 350px;
    font-size: 20px;
    line-height: 1.5;
  }
}

.form-program {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 20px;
  margin-bottom: 32px;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: #444444;
}
@media (min-width: 768px) {
  .form-program {
    row-gap: 54px;
    margin-bottom: 72px;
    font-size: 20px;
    line-height: 1.5;
  }
}
@media (min-width: 1280px) {
  .form-program {
    row-gap: 61px;
    column-gap: 80px;
    margin-bottom: 82px;
  }
}

.form-program__small-group {
  grid-column: 1/-1;
  border: 0;
  padding: 0;
  margin: 0;
}
.form-program__small-group--border-on {
  position: relative;
  padding: 22px 0 19px;
}
.form-program__small-group--border-on::before {
  position: absolute;
  left: -20px;
  top: 0;
  bottom: 0;
  width: 100vw;
  content: "";
  border-top: 1px solid #d9d9d9;
  border-bottom: 1px solid #d9d9d9;
  z-index: -1;
}
@media (min-width: 768px) {
  .form-program__small-group--border-on::before {
    display: none;
  }
}
@media (min-width: 768px) {
  .form-program__small-group--border-on {
    padding: 25px 28px 29px;
    border: 1px solid #d9d9d9;
    margin-bottom: 6px;
    margin-top: 18px;
  }
}
@media (min-width: 1280px) {
  .form-program__small-group--border-on {
    margin: 0;
    padding: 25px 28px;
  }
}
@media (min-width: 1280px) {
  .form-program__small-group {
    grid-column: span 1;
  }
}

.form-program__group {
  grid-column: 1/-1;
  border: 0;
  padding: 0;
  margin: 0;
}

.form-program__group-title {
  margin-bottom: 35px;
}
@media (min-width: 768px) {
  .form-program__group-title {
    margin-bottom: 76px;
    margin-top: 11px;
  }
}
@media (min-width: 1280px) {
  .form-program__group-title {
    margin-top: 8px;
  }
}
@media (min-width: 768px) {
  .form-program__group-title--small-margin {
    margin-bottom: 57px;
  }
}
@media (min-width: 1280px) {
  .form-program__group-title--small-margin {
    margin-top: 6px;
  }
}

.form-program__control-list {
  display: grid;
  grid-template-columns: 1fr;
  min-height: 100%;
  align-content: space-between;
  gap: 13px;
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@media (min-width: 1280px) {
  .form-program__control-list--two-column {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    row-gap: 32px;
  }
}
@media (min-width: 768px) {
  .form-program__control-list--four-column {
    grid-template-columns: 1fr 1fr;
    column-gap: 80px;
    row-gap: 32px;
  }
}
@media (min-width: 1280px) {
  .form-program__control-list--four-column {
    grid-template-columns: repeat(4, 1fr);
  }
}

.form-program__text-control {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.form-program__control-label {
  flex: 1 0 auto;
  text-transform: uppercase;
}
.form-program__control-label--shade-dark {
  color: #222222;
}
.form-program__control-label--shade-black {
  color: #000000;
}

.form-program__text-input,
.form-program__counter,
.form-program__textarea {
  width: 100%;
  font: inherit;
  color: inherit;
  text-transform: uppercase;
}

.form-program__text-input,
.form-program__counter {
  padding: 13px 14px;
  margin-left: 15px;
  border: 1px solid #e7e7e7;
}
@media (min-width: 768px) {
  .form-program__text-input,
.form-program__counter {
    padding: 10px 18px;
  }
}

.form-program__text-input--with-icon {
  padding-right: 40px;
}
@media (min-width: 1280px) {
  .form-program__text-input--with-icon {
    padding-bottom: 8px;
  }
}
.form-program__text-input::-webkit-input-placeholder {
  color: #444444;
}

.form-program__input-icon {
  position: absolute;
  right: 15px;
  top: 50%;
  transform: translateY(-50%);
  content: "";
}

.form-program__radio-control {
  display: flex;
  align-items: center;
}

.form-program__radio-input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
  cursor: pointer;
}

.form-program__radio-input + .form-program__control-mark {
  position: relative;
  margin-right: 15px;
  width: 30px;
  height: 30px;
  border: 1px solid #cdcdcd;
  border-radius: 50%;
}
@media (min-width: 768px) {
  .form-program__radio-input + .form-program__control-mark {
    width: 40px;
    height: 40px;
    margin-right: 20px;
  }
}

.form-program__radio-input:checked + .form-program__control-mark::after {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 13.5px;
  height: 13.5px;
  border-radius: 50%;
  background-color: #68b738;
  content: "";
}
@media (min-width: 768px) {
  .form-program__radio-input:checked + .form-program__control-mark::after {
    width: 18px;
    height: 18px;
  }
}

.form-program__radio-input + .form-program__control-label {
  line-height: 30px;
}

.form-program__textarea {
  resize: none;
  padding: 9px 12px;
  border-color: #e7e7e7;
}
@media (min-width: 768px) {
  .form-program__textarea {
    max-height: 260px;
  }
}

.form-program__checkbox-control {
  display: flex;
  align-items: center;
}

.form-program__checkbox-input {
  position: absolute;
  opacity: 0;
  height: 0;
  width: 0;
  cursor: pointer;
}

.form-program__checkbox-input + .form-program__control-mark {
  position: relative;
  margin-right: 16px;
  width: 40px;
  height: 40px;
  border: 1px solid #cdcdcd;
}
@media (min-width: 768px) {
  .form-program__checkbox-input + .form-program__control-mark {
    margin-right: 18px;
  }
}

.form-program__checkbox-input:checked + .form-program__control-mark {
  background-image: url("../img/bg/tick.svg");
  background-position: center center;
  background-size: 26px 26px;
  background-repeat: no-repeat;
}

.form-program__checkbox-input + .form-program__control-label {
  line-height: 30px;
}

.form-program__submit-group {
  grid-column: 1/-1;
  display: grid;
  grid-template-columns: 1fr;
  row-gap: 21px;
  border: 0;
  padding: 0;
  margin-top: 14px;
}
@media (min-width: 768px) {
  .form-program__submit-group {
    grid-template-columns: 1fr 1fr;
    align-items: center;
    gap: 80px;
    margin-top: 0;
  }
}

.form-program__submit {
  padding: 7px;
  border: 0;
}
@media (min-width: 768px) {
  .form-program__submit {
    padding: 14px;
  }
}

.form-program__endnote {
  font-family: "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: #000000;
  text-align: center;
}
@media (min-width: 768px) {
  .form-program__endnote {
    font-size: 16px;
    text-align: left;
  }
}